@media screen and (min-width:320px) and (max-width:1023px) {
    .pricing {width: 100%;}
    .pricing-one-content {width: 100%;}
    .pricing-two {flex-direction: column;align-items: center;}
    .pricing-two-1 {width: 90%;}
    .pricing-two-2 {width: 90%; margin: 10px 0;}
    .pricing-two-3 {width: 90%;}

}
@media screen and (min-width:1024px) and (max-width:1900px) {
